@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  width: 100%;
  height: 100%;

  margin: 0;
  padding: 0;
  display: table;
  position: absolute;
  font-family: 'Montserrat', serif;

  background-image: url(https://source.unsplash.com/1920x1080/?+mountain);
  background-color: #111111;
  background-size: cover;
  background-attachment: fixed;
}

#root {
  width: 100%;
  height: 100%;

  margin: 0;
  padding: 0;
  display: table;
  position: absolute;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.text_backed {
    background-color: #222222;
}

.text_backable { /* Safari */
    transition: background-color 1s;
}

#title_wrapper {
    display: table;
    height: 100vh;
}

#title_content_wrapper {
    display: table-cell;
    vertical-align: middle;
}

.name_title {
    font-size: 15vmin;
    text-align: center;

    margin: 0;

    color: #FFFDFD;
}

.role_title {
    font-size: 5vmin;
    text-align: center;

    margin: 0 0 10px;

    color: #F0E100;
}

.flashing {
    -webkit-animation-name: monoscroll;
            animation-name: monoscroll;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

#options_table {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
    table-layout: fixed;
    text-align: center;
}

.option_text {
    color: white;
    font-size: 5vmin;
}

.option_text a {
    color: white;
    text-decoration: none;
}

.option_text a:hover {
    text-decoration: overline;
}

@media only screen and (max-width: 1200px) {
    .option_text {
        font-size: 3vmin;
    }
}
#corner_tag {
    position: fixed;
    left: 0;
    top: 0;

    padding-top: 15px;
    padding-left: 15px;
}

#corner_tag a {
    color: white;
    text-decoration: overline;
}

.small_text {
    color: white;
    display: inline-block;
    clear: both;
    float: left;
    margin: 5px 0 0;
}

